@font-face {
  font-family: "Open sans";
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"),
    url("../fonts/opensans.woff2") format("woff2"),
    url("../fonts/opensans.woff") format("woff");
}

@font-face {
  font-family: "Open sans";
  font-style: normal;
  font-weight: 700;
  src: local("Roboto"),
    url("../fonts/opensansbold.woff2") format("woff2"),
    url("../fonts/opensansbold.woff") format("woff");
}

.page__body {
  display: flex;
  flex-direction: column;
  min-width: $mobile-min-width;
  margin: 0;
  padding: 0;
  font-family: "Open sans", "Arial", sans-serif;
  font-weight: 400;
  background-color: var(--basic-white);
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
