    @media (min-width: $tablet-width) {
      width: calc(100% + 100px);
      height: 17px;
    }

    @media (min-width: $desktop-width) {
      width: 100%;
      height: 23px;
    }
  }
}

.reviews__wrapper {
  position: relative;
  padding-bottom: 103px;

  @media (min-width: $tablet-width) {
    padding-top: 79px;
    padding-bottom: 47px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 175px;
    padding-bottom: 95px;
  }
}

.reviews__header {
  margin-bottom: 13px;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    align-self: flex-start;
    min-height: 53px;
    margin-bottom: 39px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 35px;
  }
}

.reviews__title {
  align-self: flex-end;
  margin: 0;
}

.reviews__button-write {
  position: absolute;
  bottom: 51px;
  width: 100%;

  @media (min-width: $tablet-width) {
    position: static;
    margin-bottom: auto;
    width: 175px;
  }
}

.reviews__item {
  margin: 0;
  padding: 0;
}

.reviews__text {
  padding: 0;
  margin: 0;
  margin-bottom: 31px;
  font-size: 15px;
  line-height: 31px;
  color: $grey-brown;

  @media (min-width: $tablet-width) {
    margin-bottom: 63px;
    font-size: 17px;
    line-height: 30px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 45px;
  }
}

.reviews__sup-container {
  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.reviews__author-info {
  display: block;
  margin: 0;
  padding: 0;
  margin-bottom: 30px;
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  color: $basic-black;

  @media (min-width: $tablet-width) and (max-width: ($desktop-width - 1px)) {
    margin-bottom: 0;
    font-size: 18px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
  }
}

.reviews__author-info a {
  @include link-reset;
  display: block;
  font-weight: normal;
  font-size: 17px;
  line-height: 30px;
  color: $grey-brown;

  &:hover {
    color: $aquamarin-dark-hover;
  }

  &:active {
    color: $aquamarin-dark-hover;
    opacity: 0.3;
  }
}

.rewiews__autor-name {
  display: block;
  margin-bottom: 3px;
}

.reviews__switch-block {
  margin-bottom: 38px;

  @media (min-width: $tablet-width) {
    margin-right: 19px;
    margin-bottom: 0;
  }
}
